<template>
  <div>
    <div style="margin-bottom: 20px;">
      <el-button size="small" @click="click_addcamera()">
        
      </el-button>
    </div>
    <el-tabs
      v-model="editableTabsValue"
      type="card"
      closable
      @tab-remove="removeTab_main()"
      @tab-click="choosetab"
    >
      <el-tab-pane
        v-for="(item, index) in editableTabs"
        :key="item.name"
        :label="item.name"
        :name="item.name"
        :lazy="true"
      >
        <h3>{{ item.name + ' content' }}</h3>
        <Tabspage />
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import Tabspage from './tabspage.vue'

export default {
  name: 'step2',
  data() {
    return {
      editableTabsValue: '#!#@3$5',
      editableTabs: [],
      NavigateItem: [],
    }
  },
  created() {
    this.initialize()
  },
  methods: {
    choosetab(tab) {
      var activeName = this.editableTabsValue
      console.log(activeName)
      console.log('' + activeName)
      this.$global_msg.tab = activeName
    },
    synchronization() {
      this.$global_msg.ruleform.channels = this.editableTabs
    },
    initialize() {
      this.editableTabs = this.$global_msg.ruleform.channels
      console.log(this.editableTabs)
    },
    removeTab_main(value) {
      console.log('')
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'success',
      })
        .then(() => {
          this.removeTab(this.editableTabsValue)
          this.synchronization()
          this.$message({
            type: 'success',
            message: '',
          })
        })
        .catch(() => {
          this.$message({
            type: 'success',
            message: '',
          })
        })
    },
    removeTab(targetName) {
      let tabs = this.editableTabs
      let activeName = this.editableTabsValue
      if (activeName === targetName) {
        tabs.forEach((tab, index) => {
          if (tab.name === targetName) {
            let nextTab = tabs[index + 1] || tabs[index - 1]
            if (nextTab) {
              activeName = nextTab.name
            }
          }
        })
      }
      this.editableTabsValue = activeName
      this.editableTabs = tabs.filter((tab) => tab.name !== targetName)
    },
    click_addcamera() {
      console.log('')
      this.$prompt(': ', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        inputValidator(value) {
          if (value == undefined || value.length === 0) {
            return ''
          } else if (!value.match('^[a-zA-Z0-9_\u4e00-\u9fa5]+$')) {
            return ''
          } else if (value.length > 20) {
            return '20'
          }
        },
        inputErrorMessage: '',
      })
        .then(({ value }) => {
          console.log('!!!')
          let newTabName = ++this.tabIndex + ''
          this.editableTabs.push({
            name: value,
          })
          this.editableTabsValue = newTabName
          this.synchronization()
          this.$notify({
            title: '',
            message: '',
            type: 'success',
          })
          this.$router.replace({
            path: '/activePublic/step2',
            name: 'step2',
          })
        })
        .catch(() => {
          console.log('')
          this.$notify({
            title: '',
            message: '',
            type: 'success',
          })
        })
    },
  },
  components: {
    Tabspage,
  },
}
</script>

