<template>
  <div class="activePublic ">
    <el-steps :space="200" :active="step" class="step">
      <el-step title="" description=""></el-step>
      <el-step title="" description=""></el-step>
    </el-steps>

    <transition name="fade">
      <div v-if="step === 1" class="step-content">
        <h3></h3>
        <p></p>
      </div>

      <div v-if="step === 2" class="step-content">
        <!--  -->
        <el-button
          type="primary"
          @click="showAddCameraDialog = true"
          style="margin-bottom: 20px"
        >
          
        </el-button>

        <!--  -->
        <el-dialog
          title=" - "
          :visible.sync="showAddCameraDialog"
          width="700px"
          @close="resetDialog"
        >
          <div
            class="image-area"
            ref="imageArea"
            @mousedown="startDrag"
            @mousemove="drag"
            @mouseup="endDrag"
            @mouseleave="endDrag"
          >
            <img
              :src="cameraImgUrl"
              alt=""
              style="width: 100%; user-select: none"
            />
            <div
              v-if="dragging || alarmRegion"
              class="alarm-region"
              :style="alarmRegionStyle"
            ></div>
          </div>

          <div style="margin-top: 10px;">
            <el-input v-model="newCameraName" placeholder="" />
          </div>

          <span slot="footer" class="dialog-footer">
            <el-button @click="showAddCameraDialog = false"></el-button>
            <el-button type="primary" @click="confirmAddCamera"></el-button>
          </span>
        </el-dialog>

        <!--  -->
        <el-card shadow="hover" style="margin-top: 20px;">
          <h3> - </h3>
          <el-menu
            :default-active="selectedCameraId ? selectedCameraId.toString() : ''"
            @select="selectCamera"
            class="camera-list"
          >
            <el-menu-item
              v-for="camera in cameras"
              :key="camera.id"
              :index="camera.id.toString()"
            >
              {{ camera.name }}
            </el-menu-item>
          </el-menu>
        </el-card>

        <!--  -->
        <div v-if="selectedCamera" style="margin-top: 20px">
          <h3>{{ selectedCamera.name }}</h3>
          <div
            style="position: relative; width: 700px; border: 1px solid #ccc"
          >
            <img
              :src="cameraImgUrl"
              alt=""
              style="width: 100%; user-select: none"
            />
            <div
              v-if="selectedCamera.alarmRegion"
              class="alarm-region"
              :style="selectedCamera.alarmRegion"
            ></div>
          </div>
        </div>
      </div>
    </transition>

    <div class="but-group">
      <el-button @click.native.prevent="handlePreview" v-show="preview">
        
      </el-button>
      <el-button @click.native.prevent="handlePreStep" v-show="preStep">
        
      </el-button>
      <el-button
        @click.native.prevent="handleNextStep"
        v-show="nextStep"
        type="primary"
      >
        
      </el-button>
      <el-button
        @click.native.prevent="handlePublish"
        v-show="publish"
        type="primary"
      >
        
      </el-button>
      <el-button @click.native.prevent="chongzhi" type="primary">
        
      </el-button>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  name: 'activePublic',
  data() {
    return {
      isRouter: false,
      preview: false,
      preStep: false,
      nextStep: true,
      publish: false,
      step: 1,

      // 
      showAddCameraDialog: false,
      cameraImgUrl: 'https://s2.loli.net/2022/12/20/EqxNvAYwcTI5Mkz.jpg',
      dragging: false,
      dragStart: { x: 0, y: 0 },
      dragEnd: { x: 0, y: 0 },
      alarmRegion: null, // 
      newCameraName: '',

      cameras: [], // 
      nextCameraId: 1,
      selectedCameraId: null,
    }
  },
  computed: {
    alarmRegionStyle() {
      if (!this.alarmRegion) return {}
      return {
        position: 'absolute',
        border: '2px dashed red',
        backgroundColor: 'rgba(255, 0, 0, 0.2)',
        top: this.alarmRegion.top + 'px',
        left: this.alarmRegion.left + 'px',
        width: this.alarmRegion.width + 'px',
        height: this.alarmRegion.height + 'px',
        pointerEvents: 'none',
      }
    },
    selectedCamera() {
      return this.cameras.find(c => c.id === this.selectedCameraId) || null
    },
  },
  methods: {
    handlePreview() {
      console.log('')
    },
    handlePreStep() {
      this.step--
      this.goStep(this.step)
      this.$router.go(-1)
      $('html,body').animate({ scrollTop: 0 }, 500)
    },
    handleNextStep() {
      this.step++
      this.goStep(this.step)
      this.$router.push('/activePublic/step' + this.step)
      $('html,body').animate({ scrollTop: 0 }, 500)
    },
    handlePublish() {
      console.log('')
      //  cameras 
      this.$message.success('')
    },
    chongzhi() {
      this.$confirm('', '', {
        type: 'warning',
      })
        .then(() => {
          this.$message.success('')
          this.step = 1
          this.goStep(this.step)
          this.cameras = []
          this.selectedCameraId = null
        })
        .catch(() => {})
    },
    goStep(n) {
      switch (n) {
        case 1:
          this.preview = false
          this.preStep = false
          this.nextStep = true
          this.publish = false
          break
        case 2:
          this.preview = false
          this.preStep = true
          this.nextStep = false
          this.publish = true
          break
      }
    },

    // 
    resetDialog() {
      this.dragging = false
      this.dragStart = { x: 0, y: 0 }
      this.dragEnd = { x: 0, y: 0 }
      this.alarmRegion = null
      this.newCameraName = ''
    },
    startDrag(e) {
      const rect = this.$refs.imageArea.getBoundingClientRect()
      this.dragging = true
      this.dragStart = { x: e.clientX - rect.left, y: e.clientY - rect.top }
      this.dragEnd = { ...this.dragStart }
      this.updateAlarmRegion()
    },
    drag(e) {
      if (!this.dragging) return
      const rect = this.$refs.imageArea.getBoundingClientRect()
      this.dragEnd = {
        x: Math.min(Math.max(e.clientX - rect.left, 0), rect.width),
        y: Math.min(Math.max(e.clientY - rect.top, 0), rect.height),
      }
      this.updateAlarmRegion()
    },
    endDrag() {
      this.dragging = false
    },
    updateAlarmRegion() {
      const top = Math.min(this.dragStart.y, this.dragEnd.y)
      const left = Math.min(this.dragStart.x, this.dragEnd.x)
      const width = Math.abs(this.dragEnd.x - this.dragStart.x)
      const height = Math.abs(this.dragEnd.y - this.dragStart.y)
      this.alarmRegion = { top, left, width, height }
    },

    confirmAddCamera() {
      if (!this.newCameraName) {
        this.$message.warning('')
        return
      }
      if (!this.alarmRegion) {
        this.$message.warning('')
        return
      }
      this.cameras.push({
        id: this.nextCameraId++,
        name: this.newCameraName,
        alarmRegion: { ...this.alarmRegion },
      })
      this.$message.success('')
      this.showAddCameraDialog = false
      this.resetDialog()
    },

    selectCamera(id) {
      this.selectedCameraId = Number(id)
    },
  },
  watch: {
    $route(to, from) {
      this.isRouter = true
    },
  },
  mounted() {
    this.goStep(this.step)
  },
}
</script>

<style scoped>
.image-area {
  position: relative;
  width: 100%;
  max-height: 400px;
  user-select: none;
  cursor: crosshair;
}

.alarm-region {
  box-sizing: border-box;
}

.camera-list {
  max-height: 300px;
  overflow-y: auto;
}

.step-content {
  padding: 20px;
}

.but-group {
  margin-top: 30px;
  text-align: right;
}

.but-group .el-button {
  margin-left: 10px;
}
</style>
