<template>
  <div class="allActive">
    <el-card class="box-card">
      <h3></h3>
      <el-button type="primary" @click="refreshLogs" size="small" style="margin-bottom: 10px;">
        
      </el-button>
      <el-table
        :data="logs"
        style="width: 100%"
        @row-click="handleRowClick"
        highlight-current-row
      >
        <el-table-column prop="id" label="ID" width="80" />
        <el-table-column prop="message" label="" />
        <el-table-column prop="timestamp" label="" width="180" />
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'LogList',
  data() {
    return {
      logs: [
        { id: 1, message: '', timestamp: '2025-07-24 10:00:00' },
        { id: 2, message: '', timestamp: '2025-07-24 10:05:23' },
        { id: 3, message: '', timestamp: '2025-07-24 10:12:45' },
      ],
    }
  },
  methods: {
    handleRowClick(row) {
      // 
      this.$message.info(`ID: ${row.id}, : ${row.message}`)
      console.log('', row)
    },
    refreshLogs() {
      // 
      this.$message.success('')
      // 
      const newId = this.logs.length + 1
      this.logs.push({
        id: newId,
        message: ` ${newId}`,
        timestamp: new Date().toLocaleString(),
      })
    },
  },
}
</script>

